*{
  box-sizing: border-box;
}


body {
  background-color: #69CCD5;
}

.texterea{
  width: 100%;
  resize: none;
  min-height: 390px;
  border: none;
  position:relative;
  outline: none;
}

.bar{
  display: flex;
  align-items: center;
  z-index: 1;
  justify-content: end;
}

.card{
  outline: none;
  padding: 0;
  border: none;
  overflow: hidden;
}

.footer-bar{
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.btn{
  height: 40px;
}

.pending{
  background-color: rgba(0, 0, 0, 0.349);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -20px;
  font-size: 23px;
}

.success{
  background-color: #16a3619f;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -20px;
  font-size: 23px;
}

.failure{
  background-color: #dc35468e;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -20px;
  font-size: 23px;
}

.add {
  width: 100px;
}

i{
  cursor: pointer;
}